body {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  font-family: sans-serif;
  margin: 0;
}

h1 {
  text-align: center;
}

p {
  color: #b3b3b3;
}

#app {
  padding: 0 20px;
  width: 100%;
}

.timeline-container {
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 40px 0;
}

.timeline-container::after {
  background-color: #282828;
  content: "";
  position: absolute;
  left: calc(50% - 2px);
  width: 4px;
  height: 100%;
}

.timeline-item {
  display: flex;
  justify-content: flex-end;
  padding-right: 30px;
  position: relative;
  margin: 10px 0;
  width: 50%;
}

.timeline-item:nth-child(odd) {
  align-self: flex-end;
  justify-content: flex-start;
  padding-left: 30px;
  padding-right: 0;
}

.timeline-item-content {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  padding: 15px;
  position: relative;
  width: 400px;
  max-width: 100%;
}

.timeline-item-content::after {
  content: " ";
  background-color: transparent;
  box-shadow: 1.5px 0px 1px rgba(0, 0, 0, 0.2);
  position: absolute;
  right: -7.5px;
  top: calc(50% - 7.5px);
  transform: rotate(45deg);
  width: 0px;
  height: 0px;
  border-right: 15px solid #181818;
  border-bottom: 15px solid transparent;
}

.timeline-item:nth-child(odd) .timeline-item-content {
  text-align: left;
  align-items: flex-start;
}

.timeline-item:nth-child(odd) .timeline-item-content::after {
  right: auto;
  left: -7.5px;
  box-shadow: 0px -1.5px 1px rgba(0, 0, 0, 0.2);
  transform: rotate(225deg);
}

.timeline-item-content .tag {
  font-size: 12px;
  font-weight: bold;
  top: 5px;
  left: 5px;
  letter-spacing: 1px;
  padding: 5px;
  position: absolute;
  text-transform: uppercase;
}

.timeline-item:nth-child(odd) .timeline-item-content .tag {
  left: auto;
  right: 5px;
}

.timeline-item-content time {
  font-weight: normal;
  color: #858585;
  font-size: 12px;
  font-weight: bold;
  margin-bottom: 5px;
}

.timeline-item-content h4 {
  line-height: 2rem;
  font-weight: normal;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0;
}

.timeline-item-content p {
  font-size: 16px;
  max-width: 250px;
  margin-top: 0;
  padding: 0;
}

.timeline-item-content a {
  color: white;
  text-decoration: none;
}

.timeline-item-content a:hover {
  text-decoration: underline;
  cursor: pointer;
}

.timeline-item-content .circle {
  background-color: #181818;
  border: 3px solid #1db954;
  border-radius: 50%;
  position: absolute;
  top: calc(50% - 10px);
  right: -40px;
  width: 20px;
  height: 20px;
  z-index: 100;
}

.timeline-item:nth-child(odd) .timeline-item-content .circle {
  right: auto;
  left: -40px;
}

@media only screen and (max-width: 1023px) {
  .timeline-item-content {
    max-width: 100%;
  }
}

@media only screen and (max-width: 767px) {
  .timeline-item-content,
  .timeline-item:nth-child(odd) .timeline-item-content {
    padding: 15px 10px;
    text-align: center;
    align-items: center;
  }

  .timeline-item-content .tag {
    width: calc(100% - 10px);
    text-align: center;
  }

  .timeline-item-content time {
    margin-top: 20px;
  }
}

